import { HttpClient ,HttpHeaders} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { AlertController,LoadingController } from 'ionic-angular';
import { HttpService } from '../../services/httpService';
/*
  Generated class for the SettingProvider provider.
  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SettingProvider {
   
   /*******************************************
   * 
   ******************************************/
   /**/
   /* */
   heartbeat = 1000 * 10;
   /*  */
   loginstate:boolean = false;
   /**/
   user :any;
   /**/
   cityColumns: any[];
   /*ID*/
   homeArea: any = {id:0,text:""};
   
   /**/
   epidemicPrevention :Object;
   keyword :string ="";
   /**/
   scanResult: any=[];
   /**/
   ltockinsert:boolean = true;
   /*loading*/
   loading;
   /**/
   formatDate = ( time: any ,date:boolean = true) => {
          const Dates = new Date( time );
          const year: number = Dates.getFullYear();
          const month: any = ( Dates.getMonth() + 1 ) < 10 ? '0' + ( Dates.getMonth() + 1 ) : ( Dates.getMonth() + 1 );
          const day: any = Dates.getDate() < 10 ? '0' + Dates.getDate() : Dates.getDate();
          const hours:any = Dates.getHours();
          const min:any = Dates.getMinutes();
          const seconds:any = Dates.getSeconds();
          let str:string = "";
          if ( date == true) {
             str =  year + '-' + month + '-' + day + " " + hours +":" + min + ":"+seconds;
           } else {
             str = year + '-' + month + '-' + day ;
           }
           return str;
         
    };
    /* */
    farmername="";
   /**/
   ltock:any = {
      code:"",
      farmId:"0",
      motherAddress:"",
      epidemicIds:"",
      slaughterAddress:"",
      slaughterRemark:"",
      saleFlow:"",
      epidemicType:"",
      epidemicProduct:"",
      epidemicBatch:"",
      healthy:1,
      insureState:2,
      type:"1",
      insureId:"1",
      accidentDead:"1",
      sex:"1",
      slaughterDate : this.formatDate( new Date().getTime() )
   };
   

   /**/
   constructor(public http: HttpService,public alertCtrl:AlertController,public loadingCtrl:LoadingController) {

   }

   /******************************************
   * 
   ******************************************/

   /*http loading*/
   presentLoading(title:string="..."){
    console.log("loading  ");
     this.loading = this.loadingCtrl.create({
        spinner: 'ios',
        content: title        
      });
   }

   /* http loading */
   dismissLoading(){
      console.log("loading  ");
      this.loading.dismiss();
   }
  
   /*  */
   presentAlert(msg:string,title:string,buttons:any[] = [{text :title==''?'':title}]) 
    {
      let alert = this.alertCtrl.create({
      title: '',
      subTitle: msg,
      cssClass:'alert-ios',
      buttons: buttons,
      enableBackdropDismiss:true
      });
      alert.present();
    return alert;
  }

   /*  */
   getEpidemicPrevention(areaid){
     return this.http.get("app/specialManager/epidemicPrevention/queryByArea/"+areaid,{});
   }

   /* heartbeat */
   getheartbeat(){
      return this.http.get("heartbeat/special_manager",{});
   }
   
}



